import { getValue } from '@testing-library/user-event/dist/utils';
import { useEffect } from 'react'
import { useState } from "react";
import Web3 from 'web3';
const CoinGecko = require('coingecko-api');
const { MerkleTree } = require('merkletreejs');
const keccak256 = require('keccak256');

const Presale = () => {

    window.userWalletAddress = null
    var amountAvax = 0

    useEffect(async() => {
        const loginButton = document.getElementById('btn-connect')
        const sendButton = document.getElementById('btn-send')
        if (!window.ethereum) {
            sendButton.style.background= "#4d4b4b";
            sendButton.disabled = true
            return false
          }
          window.web3 = new Web3(window.ethereum);
          sendButton.style.background = "#0088a9";
          const CoinGeckoClient = new CoinGecko();
          window.data = await CoinGeckoClient.coins.fetchTickers('avalanche-2', {});
          loginButton.addEventListener('click', loginWithMetaMask)
     });

    async function loginWithMetaMask() {
        const loginButton = document.getElementById('btn-connect')
        const sendButton = document.getElementById('btn-send')
        const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' })
          .catch((e) => {
            console.error(e.message)
            return
          })
        if (!accounts) { return }
        window.userWalletAddress = accounts[0]
        loginButton.innerText = window.userWalletAddress.substring(0,6) + "..."
        loginButton.style.background = "#b13333"
        
        sendButton.addEventListener('click', sendTx)
        loginButton.removeEventListener('click', loginWithMetaMask)
        setTimeout(() => {
          loginButton.addEventListener('click', signOutOfMetaMask)
        }, 200)
      }

      function signOutOfMetaMask() {
        const loginButton = document.getElementById('btn-connect')
        window.userWalletAddress = null
        loginButton.innerText = 'Connect'
        loginButton.style.background = "#0088a9";
      
        loginButton.removeEventListener('click', signOutOfMetaMask)
        setTimeout(() => {
          loginButton.addEventListener('click', loginWithMetaMask)
        }, 200)
      }

      async function sendTx() {
        let whitelistAddresses = [
            "0xe397c5D7a8B704ffd03fa72DaB73b7fCb1514218",
            "0xAb8483F64d9C6d1EcF9b849Ae677dD3315835cb2",
            "0x4B20993Bc481177ec7E8f571ceCaE8A9e22C02db" ]
        const leafNodes = whitelistAddresses.map(addr => keccak256(addr));
        const merkleTree = new MerkleTree(leafNodes, keccak256, { sortPairs: true});
        const claimingAddress = keccak256(window.userWalletAddress).toString("hex")
        // `getHexProof` returns the neighbour leaf and all parent nodes hashes that will
        // be required to derive the Merkle Trees root hash.
        const hexProof = merkleTree.getHexProof(claimingAddress);
        const CONTRACT_ADDRESS = "0x6fdC1288607E291EFDce977Fa66DF7967B89Ef33"
        const contract = new window.web3.eth.Contract(window.ABI, CONTRACT_ADDRESS);
        const response = await contract.methods.requestAllocation(hexProof).send({from: window.userWalletAddress, value: window.web3.utils.toWei(amountAvax, "ether")})
        console.log(response)
      }

      async function avaxAmount(value) {
        document.getElementById('range-indicator').innerText = value.target.value + " AVAX"
        amountAvax = value.target.value.toString()
        var tokenAmount = window.data.data.tickers[0].last * amountAvax / 0.000005
        document.getElementById('to-token').innerText = "You get: " +  tokenAmount.toFixed(0) + " $AVPR"
      }

      window.ABI = [
        {
            "anonymous": false,
            "inputs": [
                {
                    "indexed": true,
                    "internalType": "address",
                    "name": "previousOwner",
                    "type": "address"
                },
                {
                    "indexed": true,
                    "internalType": "address",
                    "name": "newOwner",
                    "type": "address"
                }
            ],
            "name": "OwnershipTransferred",
            "type": "event"
        },
        {
            "inputs": [
                {
                    "internalType": "address",
                    "name": "_address",
                    "type": "address"
                }
            ],
            "name": "checkForAnormality",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "uint256",
                    "name": "_hardCap",
                    "type": "uint256"
                },
                {
                    "internalType": "uint256",
                    "name": "_maxBuy",
                    "type": "uint256"
                },
                {
                    "internalType": "uint256",
                    "name": "_minBuy_",
                    "type": "uint256"
                }
            ],
            "name": "modifyParameters",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "bytes32",
                    "name": "_merkleRoot",
                    "type": "bytes32"
                }
            ],
            "name": "newMerkleRoot",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "renounceOwnership",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "bytes32[]",
                    "name": "_merkleProof",
                    "type": "bytes32[]"
                }
            ],
            "name": "requestAllocation",
            "outputs": [],
            "stateMutability": "payable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "address",
                    "name": "newOwner",
                    "type": "address"
                }
            ],
            "name": "transferOwnership",
            "outputs": [],
            "stateMutability": "nonpayable",
            "type": "function"
        },
        {
            "inputs": [
                {
                    "internalType": "address",
                    "name": "_address",
                    "type": "address"
                }
            ],
            "name": "checkAllocation",
            "outputs": [
                {
                    "internalType": "uint256",
                    "name": "",
                    "type": "uint256"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "getHardCap",
            "outputs": [
                {
                    "internalType": "uint256",
                    "name": "",
                    "type": "uint256"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "getMaxBuy",
            "outputs": [
                {
                    "internalType": "uint256",
                    "name": "",
                    "type": "uint256"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "getMinBuy",
            "outputs": [
                {
                    "internalType": "uint256",
                    "name": "",
                    "type": "uint256"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "merkleRoot",
            "outputs": [
                {
                    "internalType": "bytes32",
                    "name": "",
                    "type": "bytes32"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        },
        {
            "inputs": [],
            "name": "owner",
            "outputs": [
                {
                    "internalType": "address",
                    "name": "",
                    "type": "address"
                }
            ],
            "stateMutability": "view",
            "type": "function"
        }
    ]

    return (          
        <div>
            <div className="infos-container">
                <div className="price glass">
                    <p className="info-text">Price: $0.000005</p>
                </div>
                <div className="minmax glass">
                    <p className="info-text">Min: 1 AVAX Max: 6 AVAX</p>
                </div>
                <div className="softcap glass">
                    <p className="info-text">SoftCap: 600 AVAX</p>
                </div>
                <div className="hardcap glass">
                    <p className="info-text">HardCap: 1000 AVAX</p>
                </div>
            </div>
            <div className="main-container glass">
                <div className="main-container-title">
                    <h1>PRESALE</h1>
                </div>
                <div className="center-container">
                    <p className="info-text ongoing">LIVE</p>
                    <div className="soft-cap-indicator">
                        <p className="indicator-text">| 600 AVAX</p>
                    </div>
                    <div className="hard-cap-indicator">
                        <p className="indicator-text">| 1000 AVAX</p>
                    </div>
                    <div id="progressbar">
                        <div></div>
                    </div>
                    <p className="to-token info-text" id="to-token">You get: 0 $AVPR</p>
                    <input type="range" min="1" max="6" value="1" className="presale-range" id='presale-range' onChange={avaxAmount}></input>
                    <p className="presale-range-indicator info-text" id="range-indicator">6 AVAX</p>
                </div>
                <div className="main-container-button">
                    <button id="btn-connect">Connect</button>
                    <button id="btn-send">Send</button>
                </div>
            </div>
            <script src="init.js"></script> 
        </div>
        
     );
}
 
export default Presale;